@import "../../inc/vars";
.acrobits_simple_hero {
	padding: 58px 0;
	text-align: center;
	@media (min-width: $screen-sm) {
		padding: 86px 0;
	}
	&__subtitle {
		color: $purple;
		@include pop(400, 20, 1.36);
		margin-bottom: 20px;
		@media (min-width: $screen-sm) {
			margin-bottom: 31px;
			font-size: 27px;
		}
	}
	.title-box {
		padding: 0 24px;
		width: 1270px;
		max-width: 100%;
		margin: 0 auto;
	}
	&__title {
		@include titleGrad();
		@include pop(400, 32, 1.24);
		margin-bottom: 22px;
		@media (min-width: $screen-sm) {
			margin-bottom: 34px;
			font-size: 53px;
		}
	}	
	&__description {
		color: #4F4B58;
		@include fig(400, 18, 1.56);
		margin-bottom: 32px;
		@media (min-width: $screen-sm) {
			font-size: 24px;
			line-height: 1.24;
			padding: 0 50px;
		}
	}
	&__button_wrap {
		margin-bottom: 42px;
		display: flex;
		justify-content: center;
		@media (min-width: $screen-sm) {
			margin-bottom: 57px;
		}
		.button {
			@include greenButton();
			margin: 0 auto;
			height: 42px;
			width: auto;
			@media (min-width: $screen-sm) {
				height: 64px;
			}
		}
	}
	&__image {
		padding: 0 33px;
		position: relative;
		overflow: hidden;
		&:before {
			content: '';
			width: 100%;
			height: 100%;
			display: block;
			position: absolute;
			left: 0;
			top: -15px;
			background: url('./../../../../images/svg/lines.svg') no-repeat;
			background-position: 30% 60%;
			@media (min-width: $screen-sm) {
				top: 0;
				background-position: center 60%;
			}
		}
		img {
			width: 722px;
			max-width: 100%;
			position: relative;
			z-index: 2;
		}
	}
}