// breakpoints
$screen-xs: 480px !default;
$screen-sm: 767px !default;
$screen-md: 1025px !default;
$screen-mlg: 1400px !default;
$screen-lg: 1680px !default;

$blue: #4452e3;
$black: #24272e;
$green: #27b989;
$purple: #6436bf;

@mixin fig($fw: normal, $fz: 16px, $lh: 100%, $fontStyle: normal) {
  font-family: "Figtree", sans-serif;
  font-style: $fontStyle;
  font-display: swap;
  font-weight: $fw;
  font-size: $fz + px;
  line-height: $lh;
}

@mixin pop($fw: normal, $fz: 16px, $lh: 100%, $fontStyle: normal) {
  font-family: "Poppins", sans-serif;
  font-style: $fontStyle;
  font-display: swap;
  font-weight: $fw;
  font-size: $fz + px;
  line-height: $lh;
}

@mixin ibm($fw: normal, $fz: 16px, $lh: 100%, $fontStyle: normal) {
  font-family: monospace;
  //font-family: "IBM Plex Mono", monospace;
  font-style: $fontStyle;
  font-display: swap;
  font-weight: $fw;
  font-size: $fz + px;
  line-height: $lh;
}


@mixin titleGrad() {
  background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

@mixin greenButton() {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: center;
  height: 64px;
  padding: 0px 16px;
  letter-spacing: 0.16px;
  @include pop(500, 16, 1);
  border-radius: 6px;
  background: $green;
  box-shadow: 0px 1px 3px 0px rgba(39, 168, 125, 0.40), 0px 4px 8px 0px rgba(39, 168, 125, 0.20), 0px 6px 20px 0px rgba(39, 168, 125, 0.15);
  transition: .3s;
  color: #fff;
  text-decoration: none;
  &:hover {
    background: #24a87d;
  }
  @media (min-width: $screen-sm){
    width: 240px;
  }
}
